.hero-container{
    background: #0c0c0c;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 30px;
    height: 92vh;
    position: relative;
    z-index: 1;
}
.hero-container::before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: linear-gradient(180deg, rgba(0,0,0,0,2) 0%, rgba(0,0,0,0,6) 100%), linear-gradient(180deg, rgba(0,0,0,0,2) 0%, transparent 100%);
    z-index: 2;
}

.hero-bg {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    height: 100%;
    width: 100%;
    overflow: hidden;
}

.video-bg {
    width: 100%;
    height: 100%;
    object-fit: cover;
    background: #232a34;
}

.hero-content {
    z-index: 3;
    max-width: 1200px;
    position: absolute;
    padding: 8px 24px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.hero-h1 {
    color: #fff;
    font-size: 48px;
    text-align: center;
}
@media screen and (max-width: 480px){
    .hero-h1 {
        font-size: 32px;
    }
    .hero-p{
        font-size: 18px;
    }
}

.hero-p {
    margin-top: 24px;
    color: #fff;
    font-size: 24px;
    text-align: center;
    max-width: 600px;
}
@media screen and (max-width: 768px){
    .hero-p{
        font-size: 24px;
    }
}
.hero-btn-wrapper {
    margin-top: 32px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.arrow-forward {
    margin-left: 8px;
    font-size: 20px;
}
.arrow-right {
    margin-left: 8px;
    font-size: 20px;
}

.button {
    padding: 22px 30px;
    font-size: 16px;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 0.2s ease-in-out;
    text-decoration: none;
}
.primary{
    background: '#01bf71'
}
.big{
    padding: 14px 48px;
}
.dark{
    color: #010606;
}
.fontBig{
    font-size: '20px';
}